import React, { Component } from 'react';
import PropTypes from 'prop-types';
import pinyin from 'chinese-to-pinyin';
import {
  Row,
  Col,
  FormGroup,
  Label,
} from 'reactstrap';
import axios from 'axios';
import styled from 'styled-components';
import TextareaAutosize from 'react-textarea-autosize';
import Loading from './loader';
import ChuHan from './ChuHan';
import { chuanHoaChinesseV2, chuanHoaChinneseNoSign, correctTyping, correctTypingChinese, isEmptyOrBlank, replaceAll } from '../../utils/utils';
import DictBook from './dict-book-data'
import * as GA from "../../utils/analystics";

function getDisplayHanViet(mean) {
  if(mean && mean["hv"]) {
    mean = mean["hv"].split(',');
    if (mean.length === 0) {
      mean = '';
    } else if (mean.length === 1) {
      mean = mean[0];
    } else {
      mean = `${mean[0]}`;
    }
   return mean;
  } else {
    GA.trackEvent('ThieuTu', 'Han', JSON.stringify( mean));
    return '';
  }
}

function processWord(chinessArray, i, dict, textScripts, bacKinhs){
  let mean;
  let word;

  if(i<chinessArray.length-5){
    word = chuanHoaChinneseNoSign(chinessArray[i]) + chuanHoaChinneseNoSign(chinessArray[i+1]) +
      chuanHoaChinneseNoSign(chinessArray[i+2]) + chuanHoaChinneseNoSign(chinessArray[i+3]) + chuanHoaChinneseNoSign(chinessArray[i+4]) + chuanHoaChinneseNoSign(chinessArray[i+5]);
    mean = dict[word];

    if(mean){
      textScripts.push({
        id: i,
        chinesse: word,
        hanViet: mean["hv"],
        bacKinh: '',
        nghiaViet: getDisplayHanViet(mean),
        nghias: mean["nghia"],
      });
      return 6;
    }
  }

  if(i<chinessArray.length-4){
    word = chuanHoaChinneseNoSign(chinessArray[i]) + chuanHoaChinneseNoSign(chinessArray[i+1]) +
      chuanHoaChinneseNoSign(chinessArray[i+2])  + chuanHoaChinneseNoSign(chinessArray[i+3])  + chuanHoaChinneseNoSign(chinessArray[i+4]);
    mean = dict[word];

    if(mean){
      textScripts.push({
        id: i,
        chinesse: word,
        hanViet: mean["hv"],
        bacKinh: bacKinhs[i] + ' ' + bacKinhs[i+1] + ' ' + bacKinhs[i+2] + ' ' + bacKinhs[i+3] + ' ' + bacKinhs[i+4],
        nghiaViet: getDisplayHanViet(mean),
        nghias: mean["nghia"],
      });
      return 5;
    }
  }

  if(i<chinessArray.length-3){
    word = chuanHoaChinneseNoSign(chinessArray[i]) + chuanHoaChinneseNoSign(chinessArray[i+1]) + chuanHoaChinneseNoSign(chinessArray[i+2])  + chuanHoaChinneseNoSign(chinessArray[i+3]);
    mean = dict[word];

    if(mean){
      textScripts.push({
        id: i,
        chinesse: word,
        hanViet: mean["hv"],
        bacKinh: bacKinhs[i] + ' ' + bacKinhs[i+1] + ' ' + bacKinhs[i+2] + ' ' + bacKinhs[i+3],
        nghiaViet: getDisplayHanViet(mean),
        nghias: mean["nghia"],
      });
      return 4;
    }
  }

  if(i<chinessArray.length-2){
    word = chuanHoaChinneseNoSign(chinessArray[i]) + chuanHoaChinneseNoSign(chinessArray[i+1]) + chuanHoaChinneseNoSign(chinessArray[i+2]);
    mean = dict[word];
    if(mean){
      textScripts.push({
        id: i,
        chinesse: word,
        hanViet: mean["hv"],
        bacKinh: bacKinhs[i] + ' ' + bacKinhs[i+1] + ' ' + bacKinhs[i+2],
        nghiaViet:  getDisplayHanViet(mean),
        nghias: mean["nghia"],
      });
      return 3;
    }
  }

  if(i<chinessArray.length-1){
    word = chuanHoaChinneseNoSign(chinessArray[i]) + chuanHoaChinneseNoSign(chinessArray[i+1]);
    mean = dict[word];
    if(mean){
      textScripts.push({
        id: i,
        chinesse: word,
        hanViet: mean["hv"],
        bacKinh: bacKinhs[i] + ' ' + bacKinhs[i+1],
        nghiaViet: getDisplayHanViet(mean),
        nghias: mean["nghia"],
      });
      return 2;
    }
  }

  mean = dict[chuanHoaChinneseNoSign(chinessArray[i])];
  let hv = ''
  if(mean) {
    hv = mean["hv"];
  }
  textScripts.push({
    id: i,
    chinesse: chinessArray[i],
    hanViet: hv,
    bacKinh: bacKinhs[i],
    nghiaViet: getDisplayHanViet(mean),
    nghias: mean?mean["nghia"]:'',
  });

  return 1;
}

function getChuHanOneWord(chinesse, dict) {
  const chinessArray = chuanHoaChinesseV2(chinesse);
  const textScripts = [];
  let mean;
  let word;
  let i = 0;

  for (i = 0; i < chinessArray.length;i++ ) {
    mean = dict[chuanHoaChinneseNoSign(chinessArray[i])];
    let hv = ''
    if(mean) {
      hv = mean["hv"];
    }
    textScripts.push({
      id: i,
      chinesse: chinessArray[i],
      hanViet: hv,
      bacKinh: '',
      nghiaViet: getDisplayHanViet(mean),
      nghias: mean?mean["nghia"]:'',
      });
    }

  return textScripts;

}

function parserScript(chinesse, dict, bacKinh) {
  const chinessArray = chuanHoaChinesseV2(chinesse);
  const textScripts = [];
  const bacKinhs = bacKinh.split(' ');
  let i = 0;

  for (i = 0; i < chinessArray.length; ) {
    i += processWord(chinessArray, i, dict, textScripts, bacKinhs);
  }

  return textScripts;

}


function getDotWord(key) {
  return [{
    id: key,
    chinesse: '.',
    hanViet: '.',
    bacKinh: '.',
    nghiaViet: '.',
    nghias: '.',
  }];
}

function parserScriptCau(cau, dict) {
  const cacDoan = replaceAll(replaceAll(replaceAll(replaceAll(cau, ',',',#'), '，', ',#'), '：',':#'), ':',':#').split('#');
  let j=0;
  let textScripts = [];
  
  for(j=0;j<cacDoan.length;j++){
    let bacKinh = pinyin(cacDoan[j]);
    let doan = parserScript(cacDoan[j], dict, bacKinh);

    console.log(cacDoan[j], doan, bacKinh);

    if(cacDoan[j]) {
      let word = doan[doan.length-1];
      if(word)
        word['dot'] = cacDoan[j].substr(cacDoan[j].length-1);
      textScripts = textScripts.concat( doan) ;
    }
  }
  if (textScripts && textScripts[0]) {
    textScripts[0]["nghiaViet"] = `${textScripts[0]["nghiaViet"].substr(0,1).toUpperCase()}${textScripts[0]["nghiaViet"].substr(1)}`;
  }
  return textScripts;
}

export default class DictComponent extends Component {
  static async getInitialProps({req}) {
    // Inherit standard props from the Page (i.e. with session data)
    let props = {};

    // If running on server, perform Async call
    if (typeof window === 'undefined') {
      try {
        props.dictBook = await DictBook.getDictBookData()
      } catch (e) {
        props.error = "Unable to fetch AsyncData on server"
      }
    }

    return props
  }

  constructor(props) {
    super(props);

    const { words } = props;

    this.state = {
      loading: true,
      nghiaViet: '',
      typingChinesse: '',
      chinesse: '',
      hanViet: '',
      bacKinh: '',
      dict: {},
    };
    // axios.get('https://tv.nhidonghocphat.com/dictClient.json').then((res) => {
    axios.get('https://raw.githubusercontent.com/thanglamhuu/nddhp.github.io/master/dictClient.json').then((res) => {
      this.setState({
        dict: res.data,
        newNghia: '',
        newWord: '',
        loading: false,
      });
    });
    this.onChangeHandlerNewWord = this.onChangeHandlerNewWord.bind(this);
    this.onChangeHandlerNewHanViet = this.onChangeHandlerNewHanViet.bind(this);
    this.onChangeHandlerNewNghia = this.onChangeHandlerNewNghia.bind(this);
    this.onChangeHandlerChuHan = this.onChangeHandlerChuHan.bind(this);
    this.onChangeHandlerHanViet = this.onChangeHandlerHanViet.bind(this);
    this.onChangeHandlerBacKinh = this.onChangeHandlerBacKinh.bind(this);
    this.onHtmlChangeNghiaViet = this.onHtmlChangeNghiaViet.bind(this);
    this.transToHanViet = this.transToHanViet.bind(this);
    this.updateNghiaViet = this.updateNghiaViet.bind(this);
    this.trans = this.trans.bind(this);
    this.updateNghia = this.updateNghia.bind(this);
    this.reportNew = this.reportNew.bind(this);
  }

  onHtmlChangeNghiaViet(e) {
    this.setState({ nghiaViet: e.target.value });
  }

  onChangeHandlerChuHan(e) {
    this.setState({ typingChinesse: e.target.value });
  }

  onChangeHandlerHanViet(e) {
    this.setState({ hanViet: correctTyping(e.target.value) });
  }

  onChangeHandlerNewWord(e) {
    const chuHan = e.target.value;
    const {dict} = this.state;
    const textScripts = getChuHanOneWord(chuHan, dict);
    let hanViet = '';
    textScripts.map(item => {hanViet += ' ' + item.hanViet; return '';});

    this.setState({ newWord: correctTyping(chuHan), newHanViet:hanViet.trim() });
  }

  onChangeHandlerNewNghia(e) {
    this.setState({ newNghia: correctTyping(e.target.value) });
  }

  onChangeHandlerNewHanViet(e) {
    this.setState({ newHanViet: correctTyping(e.target.value) });
  }

  onChangeHandlerBacKinh(e) {
    this.setState({ bacKinh: correctTyping(e.target.value) });
  }

  transToHanViet() {
    const {typingChinesse, dict} = this.state;
    let textScripts = [];
    const cacCau = replaceAll(replaceAll(replaceAll(replaceAll(typingChinesse, '.','.#'), '. ', '.#'), '? ','?#'), '。','.#').split('#');

    let i=0;
    for(i=0;i<cacCau.length;i++){
      let cau = parserScriptCau(cacCau[i], dict);
      if(cacCau[i]) {
        console.log(cacCau[i]);
        let word = cau[cau.length-1];
        cau[cau.length-1]['dot'] = cacCau[i].substr(cacCau[i].length-1);
        textScripts = textScripts.concat( cau) ;
      }
    }

    this.setState({chinesse: textScripts});
  }

  reportNew() {
    const {newWord, newHanViet, newNghia} = this.state;
    const json = `{"chuHan":"${newWord}","hanViet":"${newHanViet}","bacKinh":"${pinyin(newWord)}","nghia":"${newNghia}"},`;
    GA.trackEvent("Report", "newWord", json);
    this.setState({newWord:'', newHanViet:'', newNghia:''});
    return json;
  }

  updateNghiaViet() {
    const {typingChinesse, chinesse, dict} = this.state;
    let nghiaViet = '';

    // Build Cards for Listing
    const chuHans = chinesse.map(item => {
      let nghiaViet_lastest = item.nghiaViet||item.hanViet||'';
      if(item.dot) {
        nghiaViet += `${nghiaViet_lastest}${item.dot} `;
      } else {
        nghiaViet += `${nghiaViet_lastest} `;
      }
    });

    this.setState({
      nghiaViet,
    });
  }

  trans() {
    updateNghiaViet();
    GA.trackEvent('DichDoan', 'trans', typingChinesse);
  }

  updateNghia(i,nghia){
    let {chinesse} = this.state;
    chinesse[i].nghiaViet = nghia;
    this.setState({chinesse});
  }

  render() {
    const {
      nghiaViet,
      typingChinesse,
      chinesse,
      bacKinh,
      loading,
      dict,
      newWord,
      newHanViet,
      newNghia
    } = this.state;

    console.log (chinesse);

    // Build Cards for Listing
    let chuHans = '', i = 0;
    if (loading) {
      chuHans = <Loading />;
    } else if (chinesse) {
      chuHans = chinesse.map(item => {
        return (
          <ChuHan
            id={i++}
            key={Math.random()}
            chinesse={item.chinesse}
            hanViet={item.hanViet}
            bacKinh={item.bacKinh}
            nghias={item.nghias}
            nghiaViet={item.nghiaViet}
            updateNghia={this.updateNghia}
            dot={item.dot}
          />
        );
      });
    }

    return (
      <div>
        <Row>
          <h2 className="h1Title">Tra đoạn chữ Hán</h2>
        </Row>
        <Row>
          <FormGroup style={{width:'100%'}}>
            <Label for="inputChuHan">Nhập nội dung chữ Hán</Label>
            <TextareaAutosize
              className="txtAutoSize"
              id="inputChuHan"
              placeholder="Chữ Hán"
              value={typingChinesse}
              onChange={this.onChangeHandlerChuHan}
            />
          </FormGroup>
          <button className="btnProcess" onClick={this.transToHanViet}>Lấy từ điển</button>
        </Row>
        <Row style={{paddingTop: '10px'}}>
          <h3>Kết quả</h3>
          <div style={{width:'70%', textAlign:'center'}}>
            <button className="btnProcess" onClick={this.trans} style={{marginLeft:100}}>Lấy kết quả sang nghĩa Việt</button>
          </div>
        </Row>
        <Row>
            <div className="divResult">
              <h5>Click lên chữ để xem từ điển và chọn nghĩa Việt</h5>
              <div className="chuHanList">
                {chuHans}
              </div>
            </div>
            <div  className="divResult">
              <h4>Chỉnh sửa lại nội dung bản dịch tại đây</h4>
              <TextareaAutosize
                className="txtAutoSize"
                id="inputNghiaViet"
                placeholder="dịch nghĩa"
                value={nghiaViet}
                onChange={this.onHtmlChangeNghiaViet}
              />
            </div>
        </Row>
        <Row  style={{paddingTop: '10px'}}>
          <h5>Yêu cầu thêm từ mới</h5>
          <Col>
            <TextareaAutosize
              className="txtInput"
              id="newWord"
              placeholder="từ mới"
              value={newWord}
              onChange={this.onChangeHandlerNewWord}
            />
            <TextareaAutosize
              className="txtInput"
              id="newHanViet"
              placeholder="âm Hán Việt"
              value={newHanViet}
              onChange={this.onChangeHandlerNewHanViet}
            />
            <TextareaAutosize
              className="txtInput"
              id="newNghia"
              placeholder="nghĩa"
              value={newNghia}
              onChange={this.onChangeHandlerNewNghia}
            />
            <button className="btnProcess" onClick={this.reportNew}  style={{marginTop: '-50px'}}>Đề nghị thêm</button>
          </Col>
        </Row>
      </div>
    );
  }
}


DictComponent.propTypes = {
  error: PropTypes.string,
  words: PropTypes.shape({
    nghiaViet: PropTypes.string,
    chinesse: PropTypes.string,
    hanViet: PropTypes.string,
    bacKinh: PropTypes.string,
  }),
};

DictComponent.defaultProps = {
  error: null,
};

const mapStateToProps = state => ({
});

const mapDispatchToProps = {
};
